"use client";

import { useState, useEffect, useRef } from "react";
import Image from "next/image";
import Link from "next/link";

// Define a Track interface for type safety
interface Track {
  id: string;
  title: string;
  artist: string;
  thumbnail?: string;
}

export default function Player() {
  const [query, setQuery] = useState("");
  const [results, setResults] = useState<Track[]>([]);
  const [currentTrack, setCurrentTrack] = useState<Track | null>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [volume, setVolume] = useState(75);
  const [queue, setQueue] = useState<Track[]>([]);
  const [audioUrl, setAudioUrl] = useState<string | null>(null);
  const [currentService, setCurrentService] = useState<string>("invidious");
  const [isSearching, setIsSearching] = useState(false);
  
  // Reference to the audio element
  const audioRef = useRef<HTMLAudioElement | null>(null);
  
  useEffect(() => {
    // Initialize audio element when the component mounts
    if (typeof window !== 'undefined' && !audioRef.current) {
      audioRef.current = new Audio();
      
      // Set initial volume
      audioRef.current.volume = volume / 100;
      
      // Setup event listeners
      audioRef.current.addEventListener('ended', handleTrackEnded);
      audioRef.current.addEventListener('error', handleAudioError);
    }
    
    // Cleanup when component unmounts
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.removeEventListener('ended', handleTrackEnded);
        audioRef.current.removeEventListener('error', handleAudioError);
        audioRef.current = null;
      }
    };
  }, [volume]);
  
  // Handle track ended event (auto play next)
  const handleTrackEnded = () => {
    skipTrack();
  };
  
  // Handle audio errors
  const handleAudioError = (e: Event) => {
    console.error("Audio error:", e);
    // You could implement retry logic or fallbacks here
  };
  
  // Update audio element when volume changes
  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = volume / 100;
    }
  }, [volume]);
  
  // Update audio element when audio URL changes
  useEffect(() => {
    if (audioRef.current && audioUrl) {
      audioRef.current.src = audioUrl;
      
      if (isPlaying) {
        const playPromise = audioRef.current.play();
        if (playPromise !== undefined) {
          playPromise.catch(error => {
            console.error("Playback error:", error);
            // Handle auto-play restrictions here
          });
        }
      }
    }
  }, [audioUrl, isPlaying]);

  const handleSearch = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!query.trim()) return;

    setIsSearching(true);
    
    try {
      const response = await fetch(`/api/search?query=${encodeURIComponent(query)}`);
      const data = await response.json();
      setResults(data.tracks || []);
      
      // Set the service that was used
      if (data.service) {
        setCurrentService(data.service);
      }
    } catch (error) {
      console.error("Search error:", error);
    } finally {
      setIsSearching(false);
    }
  };

  const playTrack = async (track: Track) => {
    try {
      const response = await fetch('/api/play', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ track }),
      });
      
      if (response.ok) {
        const data = await response.json();
        
        // Set current track and update player state
        setCurrentTrack(track);
        setIsPlaying(true);
        
        // Store the audio URL for the client-side player
        setAudioUrl(data.audioUrl);
        
        // Update the service that was used
        if (data.service) {
          setCurrentService(data.service);
        }
        
        // Add to queue if not already playing
        if (!currentTrack) {
          setQueue([...queue, track]);
        }
      }
    } catch (error) {
      console.error("Play error:", error);
    }
  };

  const togglePlayPause = async () => {
    if (!currentTrack || !audioRef.current) return;
    
    try {
      const endpoint = isPlaying ? '/api/pause' : '/api/resume';
      await fetch(endpoint, { method: 'POST' });
      
      // Update client-side playback
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        await audioRef.current.play();
      }
      
      setIsPlaying(!isPlaying);
    } catch (error) {
      console.error("Toggle play/pause error:", error);
    }
  };

  const skipTrack = async () => {
    if (queue.length <= 1) return;
    
    try {
      const response = await fetch('/api/skip', { method: 'POST' });
      
      if (response.ok) {
        const data = await response.json();
        
        if (data.endOfQueue) {
          // End of queue reached, stop playback
          setCurrentTrack(null);
          setIsPlaying(false);
          setAudioUrl(null);
          if (audioRef.current) {
            audioRef.current.pause();
          }
        } else if (data.nextTrack) {
          // Play next track
          const nextTrack = data.nextTrack;
          setCurrentTrack(nextTrack);
          
          // We need to fetch the audio URL for the next track
          const playResponse = await fetch('/api/play', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ track: nextTrack }),
          });
          
          if (playResponse.ok) {
            const playData = await playResponse.json();
            setAudioUrl(playData.audioUrl);
          }
        }
        
        // Remove current track from queue and set next track as current
        const newQueue = [...queue];
        newQueue.shift();
        setQueue(newQueue);
      }
    } catch (error) {
      console.error("Skip error:", error);
    }
  };

  const handleVolumeChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const newVolume = Number.parseInt(e.target.value);
    setVolume(newVolume);
    
    try {
      await fetch('/api/volume', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ volume: newVolume }),
      });
    } catch (error) {
      console.error("Volume change error:", error);
    }
  };

  return (
    <div className="min-h-screen flex flex-col bg-background">
      {/* Header */}
      <header className="flex justify-between items-center p-6 bg-card border-b border-border sticky top-0 z-10">
        <div className="flex items-center gap-3">
          <Link href="/" className="flex items-center gap-2">
            <Image 
              src="/soundify-logo.svg" 
              alt="Soundify Logo" 
              width={32} 
              height={32} 
              className="dark:invert"
              priority
            />
            <h1 className="text-xl font-bold bg-gradient-to-r from-primary to-purple-400 bg-clip-text text-transparent">Soundify</h1>
          </Link>
        </div>
        
        <p className="text-sm text-muted-foreground">
          Powered by {currentService === 'invidious' ? 'Invidious' : currentService === 'piped' ? 'Piped' : currentService}
        </p>
      </header>

      {/* Main content */}
      <main className="flex flex-1 flex-col md:flex-row">
        {/* Search and results */}
        <div className="flex-1 p-6 border-r border-border overflow-auto">
          <form onSubmit={handleSearch} className="mb-6">
            <div className="flex gap-2">
              <div className="relative flex-1">
                <input
                  type="text"
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  placeholder="Search for songs..."
                  className="px-4 py-3 rounded-lg w-full bg-input focus:outline-none focus:ring-2 focus:ring-primary focus:ring-opacity-50 transition-all pr-10"
                />
                {query && (
                  <button 
                    type="button" 
                    onClick={() => setQuery("")}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-muted-foreground hover:text-foreground"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <title>Clear search button</title>
                      <line x1="18" y1="6" x2="6" y2="18" />
                      <line x1="6" y1="6" x2="18" y2="18" />
                    </svg>
                  </button>
                )}
              </div>
              <button 
                type="submit"
                className="px-4 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-opacity-90 transition-all flex items-center gap-2 min-w-[100px] justify-center"
                disabled={isSearching}
              >
                {isSearching ? (
                  <svg className="animate-spin h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                ) : (
                  <>
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <title>Search button</title>
                      <circle cx="11" cy="11" r="8" />
                      <line x1="21" y1="21" x2="16.65" y2="16.65" />
                    </svg>
                    Search
                  </>
                )}
              </button>
            </div>
          </form>
          
          <div className="space-y-4 animate-fadeIn">
            <h2 className="text-lg font-semibold mb-3">Results</h2>
            {results.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-12 text-center">
                <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="text-muted-foreground mb-4">
                  <circle cx="12" cy="12" r="10" />
                  <line x1="12" y1="8" x2="12" y2="12" />
                  <line x1="12" y1="16" x2="12.01" y2="16" />
                </svg>
                <p className="text-muted-foreground">Search for songs to start listening</p>
              </div>
            ) : (
              <div className="space-y-2">
                {results.map((track, index) => (
                  <button 
                    key={track.id}
                    className="flex items-center p-4 rounded-xl hover:bg-card hover:shadow-sm cursor-pointer w-full text-left transition-all animate-slideUp"
                    style={{ animationDelay: `${index * 0.05}s` }}
                    onClick={() => playTrack(track)}
                    aria-label={`Play ${track.title} by ${track.artist}`}
                    type="button"
                  >
                    <div className="w-14 h-14 relative overflow-hidden rounded-lg mr-4 flex-shrink-0 bg-muted">
                      <Image
                        src={track.thumbnail || "/placeholder-album.png"}
                        alt={track.title}
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="overflow-hidden flex-1">
                      <h3 className="font-medium truncate">{track.title}</h3>
                      <p className="text-sm text-muted-foreground truncate">{track.artist}</p>
                    </div>
                    <div className="ml-2 opacity-0 group-hover:opacity-100 hover:bg-primary hover:text-primary-foreground p-2 rounded-full transition-all">
                      <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                        <title>Play track button</title>
                        <polygon points="5 3 19 12 5 21 5 3" />
                      </svg>
                    </div>
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
        
        {/* Player */}
        <div className="w-full md:w-96 p-6 bg-card border-l border-border">
          <div className="sticky top-6">
            <h2 className="text-xl font-semibold mb-6">Now Playing</h2>
            
            {currentTrack ? (
              <div className="mb-8 animate-fadeIn">
                <div className="w-full aspect-square relative overflow-hidden rounded-xl mb-4 shadow-md bg-muted">
                  <Image
                    src={currentTrack.thumbnail || "/placeholder-album.png"}
                    alt={currentTrack.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <h3 className="font-medium text-xl line-clamp-2">{currentTrack.title}</h3>
                <p className="text-muted-foreground">{currentTrack.artist}</p>
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center py-12 text-center mb-8">
                <div className="w-32 h-32 rounded-xl bg-muted flex items-center justify-center mb-4">
                  <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="text-muted-foreground">
                    <circle cx="12" cy="12" r="10" />
                    <polygon points="10 8 16 12 10 16 10 8" />
                  </svg>
                </div>
                <p className="text-muted-foreground">No track currently playing</p>
              </div>
            )}
            
            {/* Controls */}
            <div className="space-y-6">
              {/* Play/Pause & Skip */}
              <div className="flex justify-center gap-6">
                <button
                  onClick={togglePlayPause}
                  disabled={!currentTrack}
                  className="w-16 h-16 rounded-full bg-primary text-primary-foreground flex items-center justify-center disabled:opacity-50 transition-all hover:shadow-lg hover:shadow-primary/20"
                  type="button"
                >
                  {isPlaying ? (
                    <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <title>Pause button</title>
                      <rect x="6" y="4" width="4" height="16" />
                      <rect x="14" y="4" width="4" height="16" />
                    </svg>
                  ) : (
                    <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                      <title>Play button</title>
                      <polygon points="5 3 19 12 5 21 5 3" />
                    </svg>
                  )}
                </button>
                <button
                  onClick={skipTrack}
                  disabled={queue.length <= 1}
                  className="w-16 h-16 rounded-full bg-secondary text-secondary-foreground flex items-center justify-center disabled:opacity-50 transition-all hover:bg-secondary/80"
                  type="button"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                    <title>Skip button</title>
                    <polygon points="5 4 15 12 5 20 5 4" />
                    <line x1="19" y1="5" x2="19" y2="19" />
                  </svg>
                </button>
              </div>
              
              {/* Volume */}
              <div className="bg-secondary/50 p-4 rounded-xl">
                <div className="flex items-center gap-4">
                  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="text-muted-foreground">
                    <title>Volume control</title>
                    <polygon points="11 5 6 9 2 9 2 15 6 15 11 19 11 5" />
                    {volume > 20 && (
                      <path d="M15.54 8.46a5 5 0 0 1 0 7.07" />
                    )}
                    {volume > 60 && (
                      <path d="M19.07 4.93a10 10 0 0 1 0 14.14" />
                    )}
                  </svg>
                  <input 
                    type="range" 
                    min="0" 
                    max="100" 
                    value={volume} 
                    onChange={handleVolumeChange} 
                    className="w-full"
                    aria-label="Volume control"
                  />
                  <span className="text-sm text-muted-foreground w-8 text-right">{volume}%</span>
                </div>
              </div>

              {/* Queue info */}
              {queue.length > 0 && (
                <div className="mt-8">
                  <h3 className="text-sm font-medium text-muted-foreground mb-2">Queue • {queue.length} tracks</h3>
                  <div className="max-h-48 overflow-y-auto pr-2">
                    {queue.map((track, index) => (
                      <div 
                        key={`${track.id}-${index}`} 
                        className="flex items-center py-2 text-sm border-b border-border last:border-0"
                      >
                        <div className="w-8 h-8 relative overflow-hidden rounded mr-2 flex-shrink-0 bg-muted">
                          <Image
                            src={track.thumbnail || "/placeholder-album.png"}
                            alt={track.title}
                            fill
                            className="object-cover"
                          />
                        </div>
                        <div className="overflow-hidden flex-1">
                          <p className="truncate font-medium">{track.title}</p>
                          <p className="text-xs text-muted-foreground truncate">{track.artist}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}